export default {
	data() {
		return {
			uid:"",
			swiper:[
				
			],
			detailData:"",
		}
	},
	onLoad(e) {
		// console.log(e);
		this.uid = e.uid;
		this.detailReq(e.uid);
	},
	onShow() {
		this.detailReq(this.uid);
	},
	methods: {
		async detailReq(id){
			var data = {
				uid:id,
			};
			var res = await this.$request.get('http://127.0.0.1:5000/product',data);
			this.detailData = res.data;
			console.log(res);
			this.swiper = res.data.swiper;
		},
	}
}
